//indexAnim.js
import threeAnimation from './three-animation.js';

document.addEventListener('DOMContentLoaded', () => {
    const svgElements = {
        design: document.querySelector('.design-svg svg path'),
        development: document.querySelector('.development-svg svg path'),
        touchdesigner: document.querySelector('.touchdesigner-svg svg path')
    };

    const textElements = {
        design: document.querySelector('.design-svg'),
        development: document.querySelector('.development-svg'),
        touchdesigner: document.querySelector('.touchdesigner-svg')
    };

    function getGradient(categoryName) {
        switch (categoryName) {
            case 'design':
                return 'linear-gradient(129.1deg, #FF153C 14.5%, #FF5387 39.11%, #FF8438 84.05%)';
            case 'development':
                return 'linear-gradient(56.47deg, #2654EC 7.83%, #5B5BFF 55.07%, #4D00FF 95.88%)';
            case 'touchdesigner':
                return 'linear-gradient(50deg, #FF5757 0%, #E743CC 46.31%, #5765FF 95.94%)';
            default:
                return '#ffffff';
        }
    }

    const categories = ['touchdesigner', 'design', 'development'];
    let currentIndex = 0;
    let animationInterval = null;

    function animateCategory(categoryName) {
        // Reset all categories
        Object.keys(svgElements).forEach(category => {
            const svgElement = svgElements[category];
            const textElement = textElements[category];

            if (svgElement) {
                svgElement.style.transition = 'opacity 0.5s ease-in-out';
                svgElement.style.opacity = '0';
            }
            if (textElement) {
                textElement.style.background = 'none';
                textElement.style.webkitBackgroundClip = 'none';
                textElement.style.webkitTextFillColor = '#7f7f7f';
            }
        });

        // Highlight the current category
        const svgElement = svgElements[categoryName];
        const textElement = textElements[categoryName];
        const gradient = getGradient(categoryName);

        if (svgElement) svgElement.style.opacity = '1';
        if (textElement) {
            textElement.style.background = gradient;
            textElement.style.webkitBackgroundClip = 'text';
            textElement.style.webkitTextFillColor = 'transparent';
        }

        // Update Three.js colors seulement si l'animation est initialisée
        if (threeAnimation && threeAnimation.updateColors) {
            threeAnimation.updateColors(categoryName);
        }
    }

    function loopAnimation() {
        animateCategory(categories[currentIndex]);
        currentIndex = (currentIndex + 1) % categories.length;
    }

    // Démarrer l'animation une fois que le préchargement est terminé
    window.addEventListener('videoPreloadProgress', (event) => {
        const progress = event.detail.progress;
        if (progress === 100 && !animationInterval) {
            // Démarrer l'animation
            animationInterval = setInterval(loopAnimation, 1500);
            loopAnimation();
        }
    });
});
